main {
  position: relative;
  min-height: 100vh;
}

.cards {
  margin: 5rem 1.5rem;
}
@media (min-width: 40em) {
  .cards {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    margin: 0 11%;
  }
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.25rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: 1fr 3fr;
    gap: 1.875rem;
  }
}
.cards__user {
  background-color: #1c1f4a;
  border-radius: 0.9375rem;
}
.cards__user__info {
  border-radius: inherit;
}

.cards__user {
  background-color: #1c1f4a;
  border-radius: 0.9375rem;
}
.cards__user__info {
  border-radius: inherit;
  background-color: #5847eb;
  padding: 1.875rem;
  display: flex;
  flex-direction: row;
}
@media (min-width: 40em) {
  .cards__user__info {
    padding: 2.1875rem 1.875rem;
    flex-direction: column;
  }
}
@media (max-width: 39.9375em) {
  .cards__user__info {
    align-items: center;
  }
}
.cards__user__info .image {
  border: 0.1875rem solid white;
  border-radius: 50%;
  height: 4.375rem;
  width: 4.375rem;
}
@media (min-width: 40em) {
  .cards__user__info .image {
    display: block;
    height: 5.3125rem;
    width: 5.3125rem;
  }
}
.cards__user__info .image img {
  width: 100%;
}
.cards__user__info .name {
  margin: 0 0 0 1.25rem;
}
@media (min-width: 40em) {
  .cards__user__info .name {
    margin: 2.5rem 0;
  }
}
.cards__user__info .name__report {
  font-size: 0.875rem;
  font-weight: 400;
  color: #bdc1ff;
}
@media (min-width: 40em) {
  .cards__user__info .name__report {
    font-size: 1rem;
  }
}
.cards__user__info .name__name {
  font-size: 1.5rem;
  font-weight: 300;
  color: white;
}
@media (min-width: 40em) {
  .cards__user__info .name__name {
    font-size: 2.375rem;
  }
}
.cards__user__menu {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 1.5625rem;
}
@media (min-width: 40em) {
  .cards__user__menu {
    flex-direction: column;
    gap: 1.25rem;
  }
}
.cards__user__menu .tab {
  font-family: "Rubik", sans-serif;
  font-size: 1.125rem;
  font-weight: 400;
  text-decoration: none;
  color: #6f76c8;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Rubik", sans-serif;
}

body {
  background-color: #0f1424;
}